<template>
    <v-container fluid style="">
        <v-card>
            <v-card-title class="white--text" style="background-color: #febe00;">
                <h5><v-icon>flash_on</v-icon> </h5>
            </v-card-title>
            <v-layout wrap>
                <v-flex md6>
                    <v-card-text class="title font-weight-bold">.. / </v-card-text>
                    <v-form @submit.prevent style="padding: 16px;">
                        <v-select color="accent" item-text="name" item-value="value" :items="itemsMedium" v-model="medium" :error-messages="display_errors.medium" label="  " @change="changeInstructions()"></v-select>
                        <v-text-field v-if="bkashShow" color="accent" clearable v-model="transactionNumber" :error-messages="display_errors.trxno" label=" "></v-text-field>
                        <v-text-field v-if="bankShow" color="accent" clearable v-model="branchNumber" :error-messages="display_errors.branch_number" label=" "></v-text-field>
                        <v-text-field v-if="bankShow" color="accent" clearable v-model="serialNumber" :error-messages="display_errors.serial_number" label=" "></v-text-field>
                        
                        <v-menu v-if="bankShow" v-model="datePicker" :close-on-content-click="false" :nudge-right="40" lazy transition="scale-transition" offset-y full-width min-width="290px">
                            <template v-slot:activator="{ on }">
                                <v-text-field v-model="date" label="" :error-messages="display_errors.date" readonly v-on="on"></v-text-field>
                            </template>
                            <v-date-picker v-model="date" @input="datePicker = false"></v-date-picker>
                        </v-menu>

                        <v-text-field v-if="mistrimamaAgentShow" color="accent" clearable v-model="memoNumber" :error-messages="display_errors.memo_number" label=" "></v-text-field>
                        <v-text-field v-if="mistrimamaAgentShow" color="accent" clearable v-model="agentIdNumber" :error-messages="display_errors.agent_id_number" label="  "></v-text-field>
                        <v-text-field color="accent" clearable v-model="amount" :error-messages="display_errors.amount" label=" "></v-text-field>
                        <v-btn @click="recharge" type="submit" color="primary"> </v-btn>
                    </v-form>
                </v-flex>
                <v-flex md6>
                    <v-card-text class="title font-weight-bold">{{ propInstruction.title }}</v-card-text>
                    <div v-for="instruction in propInstruction.instructions" :key="instruction" class="label-divs">{{ instruction }}</div>
                </v-flex>
            </v-layout>
        </v-card>
        <v-snackbar v-model="snackbar" :vertical="true" :bottom="'bottom'" :right="'right'" :timeout="10000">
            {{ alertMessage }}
            <v-btn class="snackButton" color="primary" flat @click="snackbar = false">Close</v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {
        mapState
    }
    from "vuex";
    import axios from "../../axios_instance";

    export default {
        data() {
            return {
                date: new Date().toISOString().substr(0, 10),
                datePicker: false,
                snackbar: false,
                alertMessage: null,
                transactionNumber: null,
                branchNumber: null,
                serialNumber: null,
                date: null,
                memoNumber: null,
                agentIdNumber: null,
                amount: null,
                medium: 'bkash',
                bkashShow: true,
                bankShow: false,
                mistrimamaAgentShow: false,
                itemsMedium: [{
                    name: "",
                    value: "bkash"
                }, {
                    name: " ",
                    value: "Bank Deposit"
                }, {
                    name: "  ",
                    value: "Mistrimama Agent"
                }],
                propInstruction: {
                    title: null,
                    instructions: []
                },
                mfsInstruction: {
                    'bkash': {
                        title: '     -        -',
                        instructions: [
                            "-:                ",
                            "-:       -         ",
                            "-:            -  ",
                            "-:  -        -  ",
                            "-:  -       (),   ( -    )    (     )    -  ",
                            "-:      -          -        -              "
                        ]
                    },
                    'bank deposit': {
                        title: ' -    -        -',
                        instructions: [
                            "-:                 ",
                            "-:      -  ",
                            "-:            -  ",
                            "-:  -        -  ",
                            "-:  -       ( ),     (     )    (  )    -  ",
                            "-:      -          -        -              "
                        ]
                    },
                    'mistrimama agent': {
                        title: '   -    -        -',
                        instructions: [
                            "-:       -      ",
                            "-:            ",
                            "-:            -  ",
                            "-:  -        -  ",
                            "-:  -       (  ),      (    )    (  )    -  ",
                            "-:      -          -        -              "
                        ]
                    },
                },
                display_errors: [],
            };
        },
        methods: {
            async recharge() {
                this.display_errors = [];
                try {
                    let response = await axios.post("/recharge-request", {
                        trxno: this.transactionNumber,
                        branch_number: this.branchNumber,
                        serial_number: this.serialNumber,
                        date: this.date,
                        memo_number: this.memoNumber,
                        agent_id_number: this.agentIdNumber,
                        amount: this.amount,
                        medium: this.medium
                    });
                    this.alertMessage = response.data.message;
                    this.snackbar = true;
                    
                    
                    this.transactionNumber = null;                 
                    this.branchNumber = null;
                    this.serialNumber = null;
                    this.date = null;
                    this.memo_number = null;
                    this.agent_id_number = null;
                    this.amount = null;
                    this.medium = 'bkash';
                } catch (error) {
                    if(error.response.data.errors){
                        this.display_errors = error.response.data.errors;
                    }
                    this.alertMessage = error.response.data.message;
                    this.snackbar = true;
                }
            },
            changeInstructions()
            {
                var d = this.medium.toLowerCase();
                this.propInstruction.title = this.mfsInstruction[d].title;
                this.propInstruction.instructions = this.mfsInstruction[d].instructions;

                this.bkashShow = false;
                this.bankShow = false;
                this.mistrimamaAgentShow = false;
                if(d == 'bkash')
                {
                    this.bkashShow = true;
                }
                else if(d == 'bank deposit')
                {
                    this.bankShow = true;
                }
                else if(d == 'mistrimama agent')
                {
                    this.mistrimamaAgentShow = true;
                }
            }
        },
        created() { 
            this.changeInstructions();
        },
        watch: {},
        computed: {}
    };
</script>
<style  scoped>
.label-divs {
    margin: 15px;
}
</style>